/*================================================================

Mixes, variables, and bolierplate junk

================================================================*/

@mixin flex-center{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
  list-style: none;
}

@mixin transition{
  -webkit-transition: all 200ms ease-out 200ms;
  -moz-transition: all 200ms ease-out 200ms;
  -o-transition: all 200ms ease-out 200ms;
  transition: all 200ms ease-out 200ms;
}



// color consistency
// ---------------------------
$color-action: #ff6600;

$color-text-on-dark: #d4d4d4;

$blue:#000cb3;

$main-c:#000cb3;

$orange: #ef7f2c;

$lightblue:#c2ddf9;

$main: #666;

$l-blue:lighten($blue, 15%);



// effects
// ---------------------------
@mixin text-shadow($horizOffset, $vertOffset, $blur, $color) {
  -webkit-text-shadow: $horizOffset $vertOffset $blur $color;
  -moz-text-shadow: $horizOffset $vertOffset $blur $color;
  -ms-text-shadow: $horizOffset $vertOffset $blur $color; /* IE 10+ */
  -o-text-shadow: $horizOffset $vertOffset $blur $color;
  text-shadow: $horizOffset $vertOffset $blur $color;
}
