.page-template-bk-ah-landing-page.sm-addr{

  .r3{

   br{
    display:none;
  }

   h4{
    display:inline-block;
   margin-bottom:0;
  }

  h3{
    display:inline-block;
    margin-bottom:0;

    a{
color:#000;
border-radius:5px;
font-size:1.2rem; 
font-weight:700;
}
  }

  

  .r3-r1{
    border-top:2px solid #dadada;
    border-bottom:2px solid #dadada;
    padding:.5rem;
  }
  }
}

