/*===================================================

Mobile Mojo

=====================================================*/
@media (max-width:1023px){

.page-template-bk-ah-landing-page{
//Hide the admin bar for screenshots
 #wpadminbar{
  display:none;
 }


  .r1{

    h2{
      text-align:center;
      padding-bottom:.5rem;
    }
  }

  .r2{

    h3{
      font-size:1.0rem;
      font-weight:700;

      br{
        display:none;
      }
    }
  }
}

.page-template-bk-ah-landing-page.stack{

  .r1{

    h2{
      text-align:center;
    }

    .addrs{

      h4{
        display:none;
      }

      .medium-6{
  width:100%;
  flex:none;
}
    }
  }


  }

   .page-template-bk-ah-landing-page.big-addr{

    .big-top{

      .office-locations{

        h3{
font-size:3rem;
        }

        h4{
      text-align:left;
        }
      }
    }

    .r2{

      .cta-1{

        .cta-tagline{
          display:block;  
        }

        #haaHdx{
          display:none;
        }

        .cta-phone{
          display:none;
        }
      }
    }

    .big-addr-top{

      h3{
        margin-bottom:0;
        font-size:.8rem;
      }

      h3:before{
        content:'• ';
      }

      h4{ 
        display:none;
      }
      .column{
        display:inline-block;
        flex:none;
        padding:0 .5rem 0 0;
      }
    }

  }
}

