/*================================================================

General purpose stylesheet

================================================================*/

/*================================================================

Mixes, variables, and bolierplate junk

================================================================*/

@mixin flex-center{
	isplay: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	-webkit-align-items: center;
	align-items: center;
	list-style: none;
}

@mixin transition{
	-webkit-transition: all 200ms ease-out 200ms;
	-moz-transition: all 200ms ease-out 200ms;
	-o-transition: all 200ms ease-out 200ms;
	transition: all 200ms ease-out 200ms;
}

/*================================================================

General purpose stylesheet

================================================================*/



.desk-show{
	display: inherit;
}

.mobile-show{
	display: none;
}

/*================================================================

Main Universal Styling

================================================================*/

body {
	background-image: url("../../../assets/images/bg-page.png");
	background-repeat: repeat;
	color: #000;
	font-family:sans-serif;
	@include transition;
	.content-row {
		background: #efefef url("../../../assets/images/bg-linen.jpg") repeat;
	}
	p {
		font-size:.9rem;
	}
	a { color: #000; }

	a:hover { color: $color-action; }
	ul {
		list-style: square;
		
	}
	li {
		font-size: inherit;
	}
}
.menu > li > a {
	color: white;
}
.row {
	background-color: #fff;
}

a { color: #666; }
a:hover { color: $color-action; }

h1,h2,h3,h4,h5,h6 {
	font-family: $serif;
}

td {
	padding: .5rem;
}

tr:nth-of-type(odd) {
	background-color: #f2f2f2;
}

/*================================================================

Main Desktop Styling

================================================================*/

@media (min-width:1024px){



}



/*================================================================

Main Mobile Styling

================================================================*/

@media (max-width:1023px){

	body{
		background:#fff;
		background-color:#fff;
	}

	.content-row{

		.column, .columns{
			background-color:#fff;
		}

	}

	#page .main-content{
		background-color:#fff;
	}

	#page{ 
		margin-top:1rem;
	}

	.desk-show{
		display:none;
	}

	.mobile-show{
		display:inherit;
	}

	.container{
		padding-top:2.7rem;
	}

}
